/* Header section */
#header-eat-section {
    background-image: linear-gradient( 
        to right bottom,
        rgba(0, 0, 0, 0.4),
        rgba(0, 0, 0, 0.4)),
    url(/static/media/eats3.6739acf1.jpg);
    background-attachment: fixed;
    background-size: cover;
    background-position: center;
    height: 85vh;
    border-bottom-left-radius: 50% 20%;
    border-bottom-right-radius: 50% 20%;
}
.header-eat-text-container {
    padding-top: 13%;
    color: #ffffff;
}
.header-eat-text-container .line {
    border-top: 0.2rem solid #ffffff;
    width: 5rem;
}
.header-eat-text-container h1 {
    font-size: 3rem;
    line-height: 5.5rem;
}
.header-eat-text-container h1 span {
    letter-spacing: 0.3rem;
    font-size: 5.5rem;
    color: #fe5000;
    text-transform: uppercase;
    letter-spacing: 0.5rem;
}
.header-eat-text-container p {
    line-height: 2.5rem;
    font-weight: lighter;
    text-align: justify;
}

/* places to eat section */
.place-to-eat-section {
    margin-top: 12rem;
    margin-bottom: 10rem;
}

.place-to-eat-section img {
    width: 40rem;
    height: auto;
    max-width: 100%;
}
.place-to-eat-section .text {
    align-self: center;
}
.place-to-eat-section p {
    font-weight: lighter;
    line-height: 2;
    text-align: justify;
}
/* eats section */
.title {
    font-size: 3rem;
    color: #041e42;
    text-transform: capitalize;
}
.eats-section {
    margin-top: 3rem;
    padding: 5rem 6rem;
    background-color: #f5f5f5;
    width: 75%;
    margin-bottom: 10rem;
}
.eats-container .line {
    border-top: 0.2rem solid #fe5000;
    width: 5rem;
}
.eats-container h2 {
    font-size: 2.2rem;
    color: #041e42;
}
.eats-container h2 span {
    font-size: 3rem;
}
.eats-container p {
    text-align: justify;
}
.eats-section img {
    width: 40rem;
    border-radius: 5%;
    max-width: 100%;
    object-fit: cover;
}
.eat-right {
    float: right;
}


/* MEDIA QUERIES */
@media screen and (max-width:991px) {
    .header-eat-text-container {
        padding-top: 20% !important;
    }
  
}

@media screen and (max-width:567px) {
    .header-eat-text-container {
        padding-top: 30% !important;
        padding-right: 5%;
        padding-left: 5%;
    }
    .header-eat-text-container h1 {
        font-size: 2.5rem;
    }
    .header-eat-text-container h1 span {
        font-size: 4rem;
    }
    .header-eat-text-container p br {
        display: none;
    }
    .place-to-eat-section p {
        padding: 0 3rem;
    }
    .eats-section {
        width: 100%;
        padding: 4rem 3rem;
        text-align: center;
    }
    .eats-section .line {
        margin: 0 auto;
    }
    
}

@media screen and (max-width:350px) {
    #header-eat-section {
        border-bottom-left-radius: 0% 0%;
        border-bottom-right-radius: 0% 0%;
    }
    .header-eat-text-container {
        padding-top: 38% !important;
        text-align: center;
    }
    .header-eat-text-container .line {
        margin: 0 auto;
    }
    .header-eat-text-container h1 {
        font-size: 2.5rem;
    }
    .header-eat-text-container h1 span {
        font-size: 4rem;
    }
    .header-eat-text-container p br {
        display: none;
    }
    .place-to-eat-section p {
        padding: 0;
    }
    
}
/* CSS Resets */
html {
    /* changed font to the percentage below so it would be easier to use REMs. 
    62.5% = 10px and 10px = 1rem */
    font-size: 62.5%;
    box-sizing: border-box;
    overflow-x: hidden;
}
body {
    color: #555555;
    font-size: 1.6rem;
}

/* global css */
.btn {
    border: 0.2rem solid #ffffff;
    border-radius: 2rem;
    font-size: 1.6rem;
    font-weight: bold;
    padding: 0.4rem 3rem;
    text-transform: uppercase;
    margin: 1rem 0;
    background-color: transparent;
    color: #ffffff;
}
.btn:hover {
    /* transform: translateY(-3px); */
    box-shadow: 0 1rem 2rem rgba(0, 0, 0, .2);
}

.btn:active {
    transform: translateY(-1px);
    box-shadow: 0 .5rem 1rem rgba(0, 0, 0, .2);
}

.btn:hover::after {
    transform: scaleX(1.4) scaleY(1.6);
    opacity: 0;
}

/* Navbar section */
.navbar {
  position: fixed;
  top: 0;
  width: 100%;
  z-index: 999;
  padding: 1rem 0;
}
.scroll {
  background-color: #041e42;
}
.navbar .navbar-brand img {
    width: 6rem;
    height: auto;
}
.navbar-nav .nav-item {
    padding: 0 1rem;
}
.navbar-nav .nav-link {
    color: #ffffff !important;
}
.navbar-nav .active {
    color: #fe5000 !important;
    font-weight: bold !important;
}
.navbar-nav .nav-link:hover {
    color: #fe5000;
}
.navbar-toggler {
    /* background-color: #fe5000; */
}
 .dropdown-menu {
  background-color: #041e42;
}
/* search bar */
.navbar-nav .search {
    font-size: 2rem;
    margin-top: 1rem;
    color: #ffffff;
    font-size: 25px;
    margin-top: 10px;
}
.search-box {
  /* background-color: #041e42;
  height: 4rem;
  border-radius: 4rem; */
}
.search-btn {
  width: 4.5rem;
  height: 4.5rem;
  float: right;
  border-radius: 50%;
  display: flex;
  justify-content: center;
  align-items: end;
}
.search-text {
  border: none;
  background: none;
  outline: none;
  float: left;
  /* padding: 0 1rem; */
  color: #ffffff;
  font-size: 1.6rem;
  transition: 0.4s;
  line-height: 4rem;
  width: 0rem;
}
.search-box:hover > .search-text{
  width: 20rem;
  padding: 0 1rem;
  background-color: #041e42;
  border-radius: 4rem;
}

